import React, { Component } from 'react';
import {Container} from 'react-bootstrap';
import {Link} from 'react-router-dom';
import '../App.css';

export default class Header extends Component {
  scrollToBottom(){
    console.log("Clicked!");
    window.scrollTo(0, document.body.scrollHeight);
  };
  render() {
    return (  
         <Container>
         <nav>
            <ul>
               <Link to={process.env.PUBLIC_URL + '/'}><li class="nav-links">Hello!</li></Link>
               <Link to={process.env.PUBLIC_URL + '/about'}><li class="nav-links">About me</li></Link>
               <Link to={process.env.PUBLIC_URL + '/portfolio'}><li class="nav-links">Portfolio</li></Link>
               <Link onClick={() => this.scrollToBottom()}><li class="nav-links">Hit me up!</li></Link>
            </ul>
         </nav>
         </Container>
    );
  }
}